.container{
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 10px;
}
.main{
    display: flex;
    flex-direction: column;
    margin: 30px 0;

}
.input_name{
    display: flex;
    margin-bottom: 5px;
    flex-wrap: wrap;

}
.input_name input{
    margin-left: 10px;
    background-color: transparent;
    border: 1px solid #61dafb;
    outline: none;
    border-radius: 5px;
    color: #fff;
    font-size: 24px;

}
.input_num{
    display: flex;
    margin-bottom: 5px;
    flex-wrap: wrap;
}
.input_num input{
    margin-left: 10px;
    background-color: transparent;
    border: 1px solid #61dafb;
    outline: none;
    border-radius: 5px;
    color: #fff;
    font-size: 24px;
}
.answer{
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
}